/* Search by */
.ais-search-box--powered-by-link, .aa-powered-by-link {
  display: inline-block;
  width: 45px;
  height: 16px;
  text-indent: 101%;
  overflow: hidden;
  white-space: nowrap;
  background-image: url("data:image/png;base64,");
  background-repeat: no-repeat;
  background-size: contain;
  vertical-align: middle;
}

.aa-article-hit--highlight {
  position: relative;
  z-index: 1;
}

.aa-article-hit--highlight::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: .1;
  z-index: -1;
}

/***************************/
/* autocomplete.js */
/***************************/

.clearfix {
  clear: both;
}

.algolia-autocomplete {
  width: 100%;
}

.aa-input {
  width: 100%;
  outline: none;
}

.aa-hint {
  width: 100%;
  color: #999;
}

.aa-dropdown-menu {
  width: 100%;
  min-width: 203px;
  margin-top: 9px;
  border: 1px solid #d9d9d9;
  border-radius: 3px;
  background-color: #fff;
  box-shadow: 0 1px 0 0 #ccc, 0 2px 3px 0 #e6e6e6;
  font-size: 14px;
  padding: 24px;
}

.aa-article-hit--category {
  height: 24px;
  font-size: 18px;
  border-bottom: 1px solid #e6e6e6;
  height: 18px;
  margin-bottom: 5px;
}

.aa-article-hit--category--content {
  background-color: #fff;
  padding-right: 16px;
}

.aa-article-hit .aa-article-hit--category {
  display: none;
}

.aa-article-hit.aa-article-hit__category-first .aa-article-hit--category {
  display: block;
}

.aa-article-hit--line {
  width: 100%;
  margin-top: 16px;
}

.aa-article-hit--section {
  font-weight: bold;
  display: none;
}

.aa-article-hit__section-first .aa-article-hit--section {
  display: block;
}

.aa-article-hit--title {
}

.aa-article-hit--body {
  font-size: 12px;
  color: #797979;
}

.aa-article-hit__xs .aa-article-hit--body {
  display: none;
}

.aa-article-hit__category-first.aa-article-hit__section-first .aa-article-hit--content {
  border-top: none;
}

.aa-powered-by {
  position: absolute;
  right: 24px;
  top: 14px;
  text-align: right;
  font-size: .8em;
}

.algolia-autocomplete .aa-dropdown-menu .aa-suggestion {
  cursor: pointer;
}

.algolia-autocomplete .aa-dropdown-menu .aa-suggestion.aa-cursor .aa-article-hit--content {
  background-color: #f8f8f8;
}



