.ais-search-box {
  margin-left: 30%;
  position: relative;
  &:before {
    content: "\1F50D";
    font-family: "entypo";
    font-size: 1.25em;
    line-height: 0;
    position: absolute;
    text-align: center;
    top: 18px;
    width: 2.2em;
    z-index: 1;
  }
  input {
    outline: none;
    padding-left: 32px;
  }
  .ais-search-box--powered-by {
    font-size: .8em;
    text-align: right;
    margin-top: 3px;
  }
}


#algolia-hits {
  float: left;
  width: 70%;
  min-height: 400px;
  .search-result {
    border-top: 1px solid #efefef;
    margin-top: 10px;
    padding: 7px 0 10px;
    &:first-child {
      margin-top: 0;
    }
  }
  .search-result-meta {
    padding: 0px !important;
    font-size: 0.8em;
    float: right;
  }
  .search-result-body {
    margin-top: 5px;
  }
  em {
    font-style: normal;
  }
  .search-result-link em {
    font-weight: bold;
  }
}

.ais-stats {
  margin-left: 30%;
  margin-top: 15px;
  text-transform: uppercase;
  .ais-stats--time {
    text-transform: lowercase;
    color: #BBB;
    margin-left: 2px;
  }
}

#algolia-facets {
  float: left;
  width: 25%;
  margin-right: 5%;
  min-height: 1px;
  > div {
    border: 1px solid #efefef;
  }

  .ais-header {
    text-align: left;
    text-transform: uppercase;
    font-weight: 300;
    font-size: 1em;
    border-bottom: 1px solid #efefef;
  }
  .ais-header, .ais-hierarchical-menu--body, .ais-refinement-list--body {
    padding: 10px;
  }

  .ais-hierarchical-menu--list__lvl1 {
    margin-left: 10px;
  }
  .ais-hierarchical-menu--list__lvl2 {
    margin-left: 10px;
  }
  .ais-hierarchical-menu--link {
    display: block;
    width: 80%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    cursor: pointer;
  }
  .ais-hierarchical-menu--item {
    line-height: 2em;
  }
  .ais-hierarchical-menu--item__active > div > .ais-hierarchical-menu--link {
    font-weight: bold;
    &:hover {
      text-decoration: line-through;
      border: none;
    }
  }
  .ais-hierarchical-menu--count {
    display: none;
  }

  #algolia-labels {
    margin-top: 20px;
    .ais-refinement-list--body {
      margin-top: 5px;
    }
    .ais-refinement-list--list {
      margin-left: -4px;
      margin-right: -4px;
    }
    .ais-refinement-list--item {
      display: inline-block;
      label {
        background: #efefef;
        padding: 4px 8px;
        margin: 4px;
        cursor: pointer;
      }
    }
    .ais-refinement-list--item label:hover, .ais-refinement-list--item__active label {
      text-decoration: none;
      background: #158EC2;
      color: white;
      -webkit-transition: all 0.3s ease;
      transition: all 0.3s ease;
    }
    .ais-refinement-list--item__active label:hover {
      text-decoration: line-through;
    }
    .ais-refinement-list--count, .ais-refinement-list--checkbox {
      display: none;
    }
  }
}

.ais-hits__empty {
  text-align: center;
  font-size: 1.8em;
  margin-top: 100px;
  font-style: italic;
}

#algolia-pagination {
  text-align: center;
  margin-top: 80px;
  ul.ais-pagination {
    display: inline-block;
  }
  .ais-pagination--item {
    display: inline-block;
    padding: 3px;
  }
  .ais-pagination--item__disabled {
    visibility: hidden;
  }
  .ais-pagination--item__active {
    font-weight: bold;
    background: #efefef;
  }
}

